/*=== Media Query ===*/
.theme-mode-panel-open .mode-switcher-panel-wrapper {
  transform: translateX(0) translateY(-50%);
}

.mode-switcher-panel-wrapper {
  transition: 0.4s;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.15);
}

.mode-switcher-panel {
  padding: 25px 0 30px;
  background: #fff;
  border-radius: 0 2px 2px 0;
}
.mode-switcher-panel .panel-group {
  padding: 0 30px;
}
.mode-switcher-panel .panel-group .panel-title {
  position: relative;
  margin-bottom: 12px;
  z-index: 0;
}
.mode-switcher-panel .panel-group .panel-title .title {
  display: inline-block;
  padding-right: 10px;
  color: #333;
  font-size: 14px;
  font-weight: 700;
  background: #fff;
  padding-bottom: 10px;
  margin-bottom: 0;
  border-bottom: none;
}
.mode-switcher-panel .panel-group .panel-title .title::after {
  position: absolute;
  content: "";
  left: 0;
  top: 10px;
  height: 1px;
  width: 100%;
  background: #ebebeb;
  z-index: -1;
}
.mode-switcher-panel .panel-group .color-skin {
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  margin: -7px -7px 0px;
}
.mode-switcher-panel .panel-group .color-skin .color-item {
  display: inline-block;
  position: relative;
  -webkit-box-flex: 1;
  flex: 1 0 calc(25% - 14px);
  margin: 7px;
  border-radius: 2px;
  cursor: pointer;
}
.mode-switcher-panel .panel-group .color-skin .color-item::before {
  content: "";
  display: block;
  padding-bottom: 100%;
}
.mode-switcher-panel .panel-group .color-skin .color-item::after {
  position: absolute;
  content: "";
  left: 50%;
  top: calc(50% - 5px);
  height: 7px;
  width: 12px;
  border: 2px solid #fff;
  border-top: none;
  border-right: none;
  opacity: 0;
  visibility: hidden;
  transform: translateX(-50%) rotate(-45deg);
}
.mode-switcher-panel .panel-group .color-skin .color-item.active::after {
  opacity: 1;
  visibility: visible;
}

.mode-switcher-panel .buttons button:focus {
  box-shadow: none;
  outline: none;
}

.mode-switcher-panel .buttons {
  display: flex;
  align-items: center;
  gap: 10px;
}
.mode-switcher-panel .buttons .switcher-btn {
  position: relative;
  min-height: 48px;
  min-width: 48px;
  max-height: 48px;
  max-width: 48px;
  border: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  background: white;
  cursor: pointer;
}
.mode-switcher-panel .buttons .switcher-btn::before {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 24px;
  height: 24px;
  transform: translate(-50%, -50%);
  content: "";
